var db = wx.cloud.database()
var users = db.collection('user')
// 引进公用登录js
import {login} from '../../common/list.js'
export default{
	name:"my",
	data() {
		return {
			wxlofin: false,
			username:[]
		}
	},
	
	methods:{
		getUserInfo(event){
			console.log(event)
			let user = event.detail.userInfo
			login(user)
			.then((res)=>{
				this.ifUser()
			})
			.catch((err)=>{
				console.log(err)
			})
		},
		
		
		// 请求数据库看看用户有没有登录
		ifUser(){
			users.get()
			.then((res)=>{
				console.log(res)
				if(res.data.length === 0){
					// 没有登陆过
					this.wxlofin = false
				}else{
					this.wxlofin = true
					this.username = res.data
				}
			})
			.catch((err)=>{
				console.log(err)
			})
		}
	},
	
	// 判断用户是否登录
	onShow() {
		console.log('123')
		// 请求数据库看看用户有没有登录
		this.ifUser()
	}
	
}
